import React,{Component} from 'react'
import {Link} from 'react-router-dom'

function Login (){
  return(
    <div className="login">
      <div className='log_card'>
        <p className="log_title">Ingresar</p>

        <form className="log_form">

          <div className="log_field">
            <input type='email' placeholder="Correo electrónico" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' placeholder="Contraseña" required></input>
            <div className="underline"></div>
          </div>

          <p className="log_pass">¿Olvidaste tu contraseña?</p>
          <Link to="/"><button className="log_button">Continuar</button></Link>
          <div className="log_signup">¿No tienes una cuenta? <Link to="/signup">Registrate</Link></div>
        </form>
      </div>
    </div>
  )
}


export default Login; 