import React,{Component} from 'react'
import {Link} from 'react-router-dom'

function Signup (){
  return(
    <div className="signup">
      <div className='log_card'>
        <p className="log_title">Crear cuenta</p>

        <form className="log_form">

          <div className="log_field">
            <input type='text' placeholder="Nombre" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='text' placeholder="Apellido" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='email' placeholder="Correo electrónico" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' placeholder="Contraseña" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' placeholder="Confirmar contraseña" required></input>
            <div className="underline"></div>
          </div>

          <Link to="/login"><button className="log_button">Registrarse</button></Link>
          
        </form>
      </div>
    </div>
  )
    
   
}


export default Signup; 